@charset "UTF-8";
:root {
  --font-lato: "Mohave", sans-serif;
}

:root {
  --orange: hsl(14, 100%, 65%);
  --yellow: hsl(63, 100%, 83%);
  --lightblue: hsl(225, 100%, 85%);
  --blue: hsl(240, 100%, 50%);
  --green: hsl(128, 100%, 86%);
  --lightpurple: hsl(267, 100%, 85%);
  --active: var(--orange);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  font-family: var(--font-lato);
  background-color: #eee;
}

button {
  border: none;
  box-shadow: none;
  cursor: pointer;
}

input, select {
  border: none;
}

select {
  appearance: none;
  -webkit-appearance: none;
  background-image: url("data:image/svg+xml;base64,");
  background-position: center right 0.7rem;
  background-repeat: no-repeat;
}

img {
  max-width: 100%;
  height: auto;
}

.active {
  color: var(--active) !important;
}

.img-fluid {
  width: 100%;
}

.desktop-only {
  display: none;
}
@media (min-width: 64em) {
  .desktop-only {
    display: block;
  }
}

.mobile-only {
  display: block;
}
@media (min-width: 64em) {
  .mobile-only {
    display: none;
  }
}

.mb-8 {
  margin-bottom: 8vw;
}

.has-arrow::after {
  content: " ›";
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: #000;
}

.font-sm {
  font-size: 0.85rem !important;
}

.nav {
  padding: 1.2rem 1.7rem;
  border-bottom: 2px solid #eee;
}
.nav.isOpen .logo svg {
  fill: #fff;
}
.nav.isOpen .nav__button {
  color: #fff;
}
.nav.isOpen .nav__button::before {
  border-top-color: #fff;
  transform: rotate(180deg);
  top: 19%;
}
.nav.isOpen .nav-list {
  transform: translateY(0);
}

.nav__inner {
  display: flex;
}

.logo {
  margin-right: 1.5rem;
  z-index: 100;
}
.logo svg {
  display: block;
  width: 3.7rem;
  height: 1.7rem;
  object-fit: cover;
}

.nav__button {
  position: relative;
  background: none;
  font-size: 1.1rem;
  z-index: 100;
}
.nav__button::before {
  content: "";
  position: absolute;
  top: 45%;
  right: -1rem;
  border: 0.35rem solid transparent;
  border-top-color: #000;
  transition: transform 0.3s ease-in, top 0.2s;
}

.nav-list {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow-y: auto;
  transform: translateY(-100%);
  transition: transform 0.4s ease-out;
  padding: 4.5rem 1.7rem 1.3rem;
  background: var(--blue);
  overflow-y: auto;
  color: #fff;
}
.nav-list a {
  display: block;
  color: #fff;
}
.nav-list__item {
  font-size: 1.185rem;
  margin-bottom: 1.5rem;
}
.nav-list__item--register {
  font-size: 0.875rem;
}
.nav-list .nav-list__item--more {
  display: none;
}
@media (min-width: 64em) {
  .nav-list .nav-list__item--more {
    display: inline-block;
  }
}
@media (min-width: 64em) {
  .nav-list {
    position: initial;
    transform: initial;
    background-color: #fff;
    color: #000;
    padding: 0.2rem 0 0 1rem;
    transition: none;
  }
  .nav-list a {
    color: #000;
    display: initial;
  }
  .nav-list__item {
    font-weight: 600;
    margin-bottom: 0;
  }
  .nav-list .nav-list__item--blue {
    color: var(--blue);
  }
  .nav-list__item:not(:last-child) {
    margin-right: 1.3rem;
  }
  .nav-list .nav-list__item--more {
    color: var(--orange);
    background: none;
    display: inline-flex;
    align-items: center;
  }
  .nav-list .nav-list__item--more .more-icon {
    position: relative;
    margin-left: 0.5rem;
    width: 0.7rem;
    height: 2px;
    display: inline-block;
  }
  .nav-list .nav-list__item--more .more-icon::after, .nav-list .nav-list__item--more .more-icon::before {
    content: "";
    position: absolute;
    width: 100%;
    left: 0;
    height: 100%;
    background: var(--orange);
  }
  .nav-list .nav-list__item--more .more-icon::before {
    transform: rotate(90deg);
  }
  .nav-list .nav-list__item--more.isOpened .more-icon::before {
    opacity: 0;
  }
}

@media (min-width: 64em) {
  .nav-list__primary {
    display: flex;
    justify-content: space-between;
  }
}

.nav-secondary {
  padding: 1rem 0 1rem 1.7rem;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 1000;
  background: rgba(255, 255, 255, 0.7);
  transition: transform 0.5s ease-in;
  font-weight: 500;
}
.nav-secondary a {
  font-size: 0.9rem;
}
.nav-secondary .active {
  margin-right: 1.5rem;
}

.more--mobile .more-next__wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 40px;
  background-image: linear-gradient(90deg, rgba(0, 0, 255, 0), var(--blue));
}
@media (min-width: 64em) {
  .more--mobile {
    display: none;
  }
}

.more--desktop {
  display: none;
}
@media (min-width: 64em) {
  .more--desktop {
    padding-top: 2rem;
  }
  .more--desktop.isOpened {
    display: block;
  }
  .more--desktop .more-main__item {
    display: flex;
  }
  .more--desktop .more__title {
    font-size: 1.5rem;
    font-weight: 600;
    line-height: 1.5;
    margin-bottom: 0.3rem;
  }
  .more--desktop .more-main a {
    font-size: 1rem;
    margin-right: 1rem;
    margin-bottom: 0;
  }
}

.more__title {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}

.more-main {
  padding-bottom: 1rem;
}
.more-main a {
  font-size: 0.85rem;
  line-height: 1.5;
  margin-bottom: 1rem;
}

.more-next__list {
  overflow-x: auto;
  width: 100%;
  padding-bottom: 2rem;
}

.more-next__wrapper {
  display: flex;
  position: relative;
}

.more-next__item {
  min-width: 200px;
  font-size: 0.875rem;
  margin-right: 1.5rem;
  line-height: 1.5rem;
}
.more-next__item h3 {
  font-size: 0.875rem;
}
@media (min-width: 64em) {
  .more-next__item {
    font-size: 1rem;
  }
  .more-next__item h3 {
    font-size: 1rem;
  }
}

.header {
  padding-right: 1rem;
}

.page {
  max-width: 100rem;
  min-height: 100vh;
  margin: auto;
  background-color: #fff;
}

.container {
  max-width: 85%;
  width: 83.3125rem;
  margin-left: auto;
  margin-right: auto;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main {
  padding-bottom: 8vw;
  border-bottom: 2px solid #eee;
}

.hero {
  padding: 0 1rem;
  display: grid;
  place-items: center;
  background-image: url("https://ableton-production.imgix.net/about/header.jpg?auto=format&dpr=2.0&fit=crop&fm=jpg&h=514&ixjsv=1.1.3&q=50&w=941");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 620px;
}

.hero__title {
  font-size: 8rem;
  color: #fb5a48;
  font-weight: 600;
}
@media (max-width: 39.9375em) {
  .hero__title {
    font-size: 4rem;
  }
}

.section-text, .section-media {
  max-width: 85%;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 40em) {
  .section-text, .section-media {
    max-width: 60%;
  }
}
@media (min-width: 87.5em) {
  .section-text, .section-media {
    max-width: 890px;
  }
}

.section-text p:not(:last-child) {
  margin-bottom: 1em;
}

.section-text__title {
  font-size: 1.25rem;
  line-height: 1.4;
  margin-bottom: 1.5rem;
  font-weight: 500;
}
.section-text__title a {
  color: var(--blue);
}
@media (min-width: 64em) {
  .section-text__title {
    font-size: 1.875rem;
  }
}

.section-text__body {
  font-size: 1rem;
  line-height: 1.5;
}
@media (min-width: 64em) {
  .section-text__body {
    font-size: 1.25rem;
  }
}

.section-media__inner {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%;
  margin-bottom: 1rem;
}
.section-media__inner::after {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  content: "";
  background: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 75px;
  height: 75px;
}
@media (min-width: 40em) {
  .section-media__inner::after {
    width: 100px;
    height: 100px;
  }
}

.section-media__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.section-media__caption {
  font-weight: 600;
}

.section-img {
  position: relative;
  padding: 8vw 0;
}

.section-img__wrapper {
  z-index: 10;
}

.section-img__large {
  width: 41.6vw;
  height: 41.6vw;
  background-size: cover;
  background-position: center;
  z-index: 10;
}
@media (min-width: 100em) {
  .section-img__large {
    width: 666.7px;
    height: 666.7px;
  }
}

.section-img__small {
  width: 33.3vw;
  height: 25vw;
  background-size: cover;
  background-position: center;
  z-index: 10;
}
@media (min-width: 100em) {
  .section-img__small {
    width: 533.3px;
    height: 400px;
  }
}

.section-img__work {
  max-width: 90%;
  margin-left: auto;
}

.section-img__large--about1 {
  background-image: url("https://ableton-production.imgix.net/about/photo-1.jpg?dpr=2.0&fit=crop&h=666&ixjsv=1.1.3&q=50&w=666");
}

.section-img__small--about2 {
  background-image: url("https://ableton-production.imgix.net/about/photo-2.jpg?dpr=2.0&fit=crop&h=170&ixjsv=1.1.3&q=50&w=226");
}

.section-img__small--people1 {
  background-image: url("https://ableton-production.imgix.net/about/photo-3.jpg?dpr=2.0&fit=crop&h=144&ixjsv=1.1.3&q=50&w=191");
}

.section-img__small--people2 {
  background-image: url("https://ableton-production.imgix.net/about/photo-4.jpg?dpr=2.0&fit=crop&h=144&ixjsv=1.1.3&q=50&w=191");
}

.section-img__large--people3 {
  background-image: url("https://ableton-production.imgix.net/about/photo-5.jpg?dpr=2.0&fit=crop&h=239&ixjsv=1.1.3&q=50&w=239");
}

.section-img__small--work1 {
  background-image: url("https://ableton-production.imgix.net/about/photo-6-a.jpg?dpr=2.0&fit=crop&h=370&ixjsv=1.1.3&q=50&w=494");
}

.section-img__large--work2 {
  background-image: url("https://ableton-production.imgix.net/about/photo-7.jpg?dpr=2.0&fit=crop&h=618&ixjsv=1.1.3&q=50&w=618");
}

.section-img__decor {
  position: absolute;
  height: 100%;
  width: 58.33333vw;
  right: 0;
  top: 0;
  background-color: var(--yellow);
}
@media (min-width: 100em) {
  .section-img__decor {
    width: 993px;
  }
}

.section-img__decor--people {
  left: 0;
  background-color: var(--green);
}

.section-img__decor--work {
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--lightpurple);
}

.section-jobs__img {
  height: 50vw;
  width: 100%;
  background-image: url("https://ableton-production.imgix.net/about/photo-8.jpg?crop=right&dpr=2.0&fit=crop&h=531&ixjsv=1.1.3&q=50&w=885");
  background-size: cover;
  background-position: center;
}

@media (min-width: 87.5em) {
  .section-jobs__wrapper {
    display: flex;
  }
  .section-jobs__wrapper .section-jobs__img {
    width: 50%;
    height: 41.67vw;
  }
  .section-jobs__wrapper .section-jobs__body-text {
    width: 50%;
  }
}
@media (min-width: 100em) {
  .section-jobs__wrapper .section-jobs__img, .section-jobs__wrapper .section-jobs__body-text {
    width: 666.7px;
    height: 666.7px;
  }
}

.section-jobs__body-text {
  padding: 8vw;
  background-color: var(--lightblue);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.section-jobs__body-text p {
  line-height: 1.5;
  margin-bottom: 3vw;
}
.section-jobs__body-text p, .section-jobs__body-text a {
  font-weight: 600;
  font-size: 1.25rem;
}
@media (min-width: 64em) {
  .section-jobs__body-text p, .section-jobs__body-text a {
    font-size: 1.875rem;
  }
}
.section-jobs__body-text a {
  color: var(--blue);
}
.section-jobs__body-text a::after {
  content: " ›";
}

.footer {
  font-size: 1rem;
}

.footer__col {
  padding-bottom: 8vw;
  line-height: 1.5;
}
.footer__col a {
  display: block;
}
@media (min-width: 40em) {
  .footer__col {
    padding-bottom: 4vw;
  }
}

.footer__title {
  font-size: 2rem;
  padding-bottom: 8vw;
}
@media (min-width: 40em) {
  .footer__title {
    padding-bottom: 4vw;
    font-size: 3rem;
  }
}

.footer__row .footer__subtitle {
  font-weight: 500;
}
@media (min-width: 64em) {
  .footer__row {
    display: flex;
  }
  .footer__row.footer__row--links {
    justify-content: space-between;
  }
  .footer__row .footer__col--email {
    order: 3;
  }
  .footer__row .footer__col {
    flex: 1;
  }
  .footer__row .footer__col--email, .footer__row .footer__col--language {
    flex: 1.5;
  }
}

.footer-form {
  margin-top: 0.7rem;
  display: flex;
}

.footer-form__input {
  flex: 1;
  border: none;
  background: #eee;
  padding: 0.7rem 1rem;
}

.footer-form__btn {
  width: 6rem;
  color: #fff;
  background: var(--blue);
}

.footer__links {
  display: flex;
  flex-direction: column;
}
.footer__links a {
  font-weight: 500;
}
@media (min-width: 64em) {
  .footer__links {
    flex-direction: row;
  }
  .footer__links a {
    margin-right: 1rem;
    font-size: 0.875rem;
  }
}

.footer__made {
  display: flex;
  align-items: center;
  margin-top: 8vw;
}
.footer__made p {
  font-weight: 500;
}
.footer__made .logo {
  margin-right: 0.7rem;
}
@media (min-width: 64em) {
  .footer__made {
    margin-top: 0;
  }
  .footer__made p {
    order: -1;
    margin-right: 1rem;
    font-size: 0.875rem;
  }
  .footer__made .logo {
    margin: 0;
  }
}

.footer__social {
  display: flex;
  padding-top: 1rem;
}
.footer__social a {
  margin-right: 0.7rem;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer__social i {
  color: #fff;
  padding: 0.5rem;
  font-size: 1.5rem;
}
.footer__social .facebook {
  background-color: #3278f2;
}
.footer__social .twitter {
  background-color: #1da1f3;
}
.footer__social .youtube {
  background-color: #e20e24;
}
.footer__social .instagram {
  background-color: #f77736;
}

.footer-select {
  padding-top: 1rem;
}
.footer-select select {
  padding: 0.5rem;
  background-color: #eee;
  font-weight: 500;
}

.footer-select__small {
  width: 120px;
}

.footer-select__big {
  width: 200px;
}